<template>
  <div>
    <el-card class="que_card">
      <el-form :inline="true" :model="form" ref="form" class="demo-form-inline">
        <el-form-item label="" label-width="90px" prop="subject">
          <el-select v-model="form.subject" placeholder="">
            <el-option
              v-for="item in subjectList"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" label-width="90px" prop="step">
          <el-select v-model="form.step" placeholder="">
            <el-option
              v-for="item in stepList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" label-width="90px" prop="enterprise">
          <el-select v-model="form.enterprise" placeholder="">
            <el-option
              v-for="item in enterpriseList"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" label-width="90px" prop="type">
          <el-select v-model="form.type" placeholder="">
            <el-option
              v-for="item in typeList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" label-width="90px" prop="difficulty">
          <el-select v-model="form.difficulty" placeholder="">
            <el-option
              v-for="item in difficultyList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" label-width="90px" prop="username">
          <el-input style="width:176px" v-model="form.username"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="90px" prop="status">
          <el-select v-model="form.status" placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" label-width="90px" prop="create_date">
          <el-date-picker
            type="date"
            v-model="form.create_date"
            placeholder=""
            style="width: 100%;"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="" label-width="90px" prop="title">
          <el-input style="width:471px" v-model="form.title"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button @click="search" type="primary"></el-button>
          <el-button @click="clear"></el-button>
          <el-button type="primary" @click="add">+</el-button>
        </el-form-item>
      </el-form>
    </el-card>
    <el-card style="margin-top:20px;">
      <el-table :data="list" border stripe>
        <el-table-column type="index" width="50" label="" />
        <el-table-column label="">
          <template slot-scope="scope">
            <span v-html="scope.row.title"></span>
          </template>
        </el-table-column>
        <el-table-column label=".">
          <template slot-scope="scope">
            <span v-if="scope.row.step === 1"
              >{{ scope.row.subject_name }}.</span
            >
            <span v-else-if="scope.row.step === 2"
              >{{ scope.row.subject_name }}.</span
            >
            <span v-else-if="scope.row.step === 3"
              >{{ scope.row.subject_name }}.</span
            >
          </template>
        </el-table-column>
        <el-table-column prop="type" label="" :formatter="formatType" />
        <el-table-column prop="enterprise_name" label="" />
        <el-table-column prop="username" label="" />
        <el-table-column prop="reads" label="" />
        <el-table-column label="">
          <template #default="scope">
            <span
              :style="{ color: scope.row.status === 0 ? 'red' : '#606266' }"
              >{{ scope.row.status === 0 ? '' : '' }}</span
            >
          </template>
        </el-table-column>
        <el-table-column label="" width="280px">
          <!-- template -->
          <template slot-scope="scope">
            <el-button @click="edit(scope.row)"></el-button>
            <el-button
              @click="changeStatus(scope.row.id)"
              :type="scope.row.status === 0 ? 'success' : 'info'"
              >{{ scope.row.status === 0 ? '' : '' }}</el-button
            >
            <el-button @click="del(scope.row.id, scope)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="limit"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <add-or-update
      :subjectList="subjectList"
      :enterpriseList="enterpriseList"
      :stepList="stepList"
      :typeList="typeList"
      :difficultyList="difficultyList"
      ref="addOrUpdateRef"
    />
  </div>
</template>

<script>
import AddOrUpdate from './add-or-update'
export default {
  name: 'QuestionList',
  components: {
    AddOrUpdate
  },
  data () {
    return {
      // 
      subjectList: [], // 
      enterpriseList: [], // 
      stepList: [
        // 
        { label: '', value: 1 },
        { label: '', value: 2 },
        { label: '', value: 3 }
      ],
      typeList: [
        // 
        { label: '', value: 1 },
        { label: '', value: 2 },
        { label: '', value: 3 }
      ],
      difficultyList: [
        // 
        { label: '', value: 1 },
        { label: '', value: 2 },
        { label: '', value: 3 }
      ],
      options: [
        // 
        {
          value: 0,
          label: ''
        },
        {
          value: 1,
          label: ''
        }
      ],
      form: {
        title: '', // 
        subject: '', // id
        enterprise: '', // id
        type: '', // :1(),2(),3()
        step: '', // :1(),2(),3()
        username: '', // 
        status: '', // 
        difficulty: '', // : 1(),2(),3()
        create_date: '' // 
      },
      page: 1, // 
      limit: 2, // 
      list: [], // 
      total: 0 //  
    }
  },
  mounted () {
    this.getSubjectListData()

    this.getEnterpriseListData()

    this.getQuestionListData()
  },
  methods: {
    async getSubjectListData () {
      const res = await this.$axios.get('/subject/list')

      this.subjectList = res.data.items
    },
    async getEnterpriseListData () {
      const res = await this.$axios.get('/enterprise/list')

      this.enterpriseList = res.data.items
    },
    async getQuestionListData () {
      const res = await this.$axios.get('/question/list', {
        params: {
          ...this.form,
          page: this.page,
          limit: this.limit
        }
      })

      if (res.code === 200) {
        this.list = res.data.items
        this.total = res.data.pagination.total
      }
    },
    search () {
      this.page = 1
      this.getQuestionListData()
    },
    clear () {
      this.$refs.form.resetFields()

      this.search()
    },
    async changeStatus (id) {
      const res = await this.$axios.post('/question/status', { id })
      if (res.code === 200) {
        this.$message.success('~')

        this.getQuestionListData()
      }
    },
    del (id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(async () => {
          const res = await this.$axios.post('/question/remove', { id })

          if (res.code === 200) {
            this.$message.success('~')

            this.search()
          }
        })
        .catch(() => {})
    },
    // 
    formatType (row, column, cellValue, index) {
      let typeName = ''
      switch (cellValue) {
        case 1:
          typeName = ''
          break
        case 2:
          typeName = ''
          break
        case 3:
          typeName = ''
          break
        default:
          break
      }

      return typeName
    },
    // 
    handleSizeChange (val) {
      this.limit = val

      this.search()
    },
    // 
    handleCurrentChange (val) {
      this.page = val

      this.getQuestionListData()
    },
    // 
    add () {
      this.$refs.addOrUpdateRef.dialogVisible = true
      this.$refs.addOrUpdateRef.mode = 'add'
    },
    // 
    edit (row) {
      this.$refs.addOrUpdateRef.dialogVisible = true

      this.$refs.addOrUpdateRef.$nextTick(() => {
        this.$refs.addOrUpdateRef.mode = 'edit'
        this.$refs.addOrUpdateRef.form = JSON.parse(JSON.stringify(row))
        // 
        this.$refs.addOrUpdateRef.form.city = this.$refs.addOrUpdateRef.form.city.split(
          ','
        )
        this.$refs.addOrUpdateRef.form.multiple_select_answer = this.$refs.addOrUpdateRef.form.multiple_select_answer.split(
          ','
        )
        // 
        if (row.select_options.length === 0) {
          this.$refs.addOrUpdateRef.form.select_options = [
            // 
            {
              label: 'A',
              text: '',
              image: ''
            },
            {
              label: 'B',
              text: '',
              image: ''
            },
            {
              label: 'C',
              text: '',
              image: ''
            },
            {
              label: 'D',
              text: '',
              image: ''
            }
          ]
        }

        this.$refs.addOrUpdateRef.$refs.myQuillEditor.quill.enable(false) // 
        this.$refs.addOrUpdateRef.$refs.myQuillEditor.quill.blur()
        this.$refs.addOrUpdateRef.$refs.myQuillEditor1.quill.enable(false) // 
        this.$refs.addOrUpdateRef.$refs.myQuillEditor1.quill.blur()
        setTimeout(() => {
          this.$refs.addOrUpdateRef.$refs.myQuillEditor.quill.enable(true) // 2
          this.$refs.addOrUpdateRef.$refs.myQuillEditor1.quill.enable(true) // 2
        }, 2000)
      })
    }
  }
}
</script>

<style lang="less">
.el-table--border th.gutter:last-of-type {
  display: block !important;
  width: 17px !important;
}
</style>
