<template>
  <div class="question">
    <el-dialog :visible.sync="dialogVisible" fullscreen>
      <div slot="title" style="fontSize:20px;color:#fff">
        {{ this.mode === 'add' ? '' : '' }}
      </div>
      <el-form
        class="form"
        :model="form"
        ref="form"
        :rules="rules"
        label-width="120px"
        :inline="false"
        size="normal"
        label-position="left"
      >
        <el-form-item label="" prop="subject">
          <el-select
            style="width:350px"
            v-model="form.subject"
            placeholder=""
          >
            <el-option
              v-for="item in subjectList"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="step">
          <el-select
            style="width:350px"
            v-model="form.step"
            placeholder=""
          >
            <el-option
              v-for="item in stepList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="enterprise">
          <el-select
            style="width:350px"
            v-model="form.enterprise"
            placeholder=""
          >
            <el-option
              v-for="item in enterpriseList"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="city">
          <el-cascader
            size="large"
            :options="options"
            :props="{ value: 'label' }"
            v-model="form.city"
          >
          </el-cascader>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-radio-group v-model="form.type">
            <!-- labelform.type -->
            <el-radio
              v-for="item in typeList"
              :key="item.value"
              :label="item.value"
              >{{ item.label }}</el-radio
            >
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="difficulty">
          <el-radio-group v-model="form.difficulty">
            <!-- label -->
            <el-radio
              v-for="item in difficultyList"
              :key="item.value"
              :label="item.value"
              >{{ item.label }}</el-radio
            >
          </el-radio-group>
        </el-form-item>
        <hr class="hrMargin" />
        <el-form-item class="setMargin" label="" prop="title">
          <quill-editor
            :options="{ placeholder: '...' }"
            v-model="form.title"
            ref="myQuillEditor1"
            @blur="onEditorBlur('title')"
          />
        </el-form-item>
        <el-form-item
          :label="labelOption[form.type]"
          :prop="propOption[form.type]"
        >
          <!--  -->
          <question-type :form="form" />
        </el-form-item>
        <hr class="hrMargin" />
        <el-form-item label="" prop="video">
          <upload-file type="video" :obj="form" />
        </el-form-item>
        <el-form-item class="setMargin" label="" prop="answer_analyze">
          <quill-editor
            :options="{ placeholder: '...' }"
            ref="myQuillEditor"
            v-model="form.answer_analyze"
            @blur="onEditorBlur('answer_analyze')"
          />
        </el-form-item>
        <el-form-item>
          <el-button @click="dialogVisible = false"></el-button>
          <el-button type="primary" @click="submit"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import { regionData } from 'element-china-area-data'
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import { quillEditor } from 'vue-quill-editor'
import UploadFile from './upload-file'
import QuestionType from './question-type'
export default {
  name: 'QuestionEdit',
  components: {
    quillEditor,
    UploadFile,
    QuestionType
  },
  props: {
    // 
    subjectList: {
      type: Array,
      default: function () {
        return []
      }
    },
    // 
    enterpriseList: {
      type: Array,
      default: function () {
        return []
      }
    },
    // 
    stepList: {
      type: Array,
      default: function () {
        return []
      }
    },
    // 
    typeList: {
      type: Array,
      default: function () {
        return []
      }
    },
    // 
    difficultyList: {
      type: Array,
      default: function () {
        return []
      }
    }
  },
  data () {
    return {
      options: regionData, // 
      dialogVisible: false,
      mode: '',
      labelOption: {
        // 
        1: '',
        2: '',
        3: ''
      },
      propOption: {
        1: 'single_select_answer',
        2: 'multiple_select_answer',
        3: 'short_answer'
      },
      // form
      form: {
        title: '', // 
        subject: '', // id
        step: '', // 
        enterprise: '', // id
        city: [], // 
        type: 1, // 123
        difficulty: 1, //  1 2 3
        single_select_answer: 'A', // 
        multiple_select_answer: [], // 
        short_answer: '', // 
        video: '', // 
        answer_analyze: '', // 
        remark: '', // 
        select_options: [
          // 
          {
            label: 'A',
            text: '',
            image: ''
          },
          {
            label: 'B',
            text: '',
            image: ''
          },
          {
            label: 'C',
            text: '',
            image: ''
          },
          {
            label: 'D',
            text: '',
            image: ''
          }
        ]
      },
      rules: {
        subject: [
          { required: true, message: '', trigger: 'change' }
        ],
        step: [{ required: true, message: '', trigger: 'change' }],
        enterprise: [
          { required: true, message: '', trigger: 'change' }
        ],
        city: [{ required: true, message: '', trigger: 'change' }],
        type: [{ required: true, message: '', trigger: 'change' }],
        difficulty: [
          { required: true, message: '', trigger: 'change' }
        ],
        title: [
          // trigger:'blur'element-ui
          { required: true, message: '' }
        ],
        answer_analyze: [
          // trigger:'blur'element-ui
          { required: true, message: '' }
        ],
        single_select_answer: [
          { required: true, message: '', trigger: 'change' }
        ],
        multiple_select_answer: [
          { required: true, message: '', trigger: 'change' }
        ],
        short_answer: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  mounted () {
    // this.$refs.myQuillEditor.quill.enable(false)
  },
  watch: {
    'form.type' (newValue) {
      this.$refs.form.clearValidate([
        'single_select_answer',
        'multiple_select_answer',
        'short_answer'
      ])
      // 
      this.$refs.form.validateField(this.propOption[newValue])
    },
    dialogVisible (newValue) {
      if (!newValue) {
        this.$refs.myQuillEditor.quill.blur()
        this.$refs.myQuillEditor1.quill.blur()
        this.$refs.form.resetFields()
        // 
        this.form.select_options = [
          // 
          {
            label: 'A',
            text: '',
            image: ''
          },
          {
            label: 'B',
            text: '',
            image: ''
          },
          {
            label: 'C',
            text: '',
            image: ''
          },
          {
            label: 'D',
            text: '',
            image: ''
          }
        ]

        // A
        this.form.single_select_answer = 'A'

        // 
        this.form.short_answer = ''
      }
    }
  },
  methods: {
    // 
    onEditorBlur (fieldName) {
      // 3
      this.$refs.form.validateField(fieldName)
    },
    submit () {
      this.$refs.form.validate(async valid => {
        if (!valid) return
        let url = ''
        if (this.mode === 'add') {
          url = '/question/add'
        } else {
          // 
          this.form.city = this.form.city.join(',')
          url = '/question/edit'
        }

        const res = await this.$axios.post(url, this.form)

        if (res.code === 200) {
          // 
          this.$message.success(this.mode === 'add' ? '~' : '~')

          // 
          this.dialogVisible = false

          // search
          this.$parent.search()
        }
      })
    }
  }
}
</script>

<style lang="less">
.question {
  .form {
    width: 832px;
    margin: 0 auto;
  }
  .hrMargin {
    margin-bottom: 20px;
  }
  .ql-editor {
    height: 200px !important;
  }
  .setMargin {
    .el-form-item__content {
      margin-left: 0 !important;
      margin-top: 40px;
    }
  }
}
</style>
